	            lines: this.lines,
	            ybase: this.ybase,
	            ydisp: this.ydisp,
	            x: this.x,
	            y: this.y,
	            scrollTop: this.scrollTop,
	            scrollBottom: this.scrollBottom,
	            tabs: this.tabs
